<template>
<div class="cs_zddetail">
	<div class="_name"><span>站点详情</span><i class="el-icon-arrow-right" @click="f_showzddetail"></i></div>
	<div class="_lis clearfix"><span class="_left">站点名称：<em>{{datalist.entityName}}</em></span><span class="_right">联系电话：<em>{{datalist.telphone}}</em></span></div>
	<div class="_lis clearfix"><span class="_left">责任民警：<em>{{datalist.liabPolicy}}</em></span><span class="_right">联系电话：<em>{{datalist.telphone}}</em></span></div>
	<div class="_lis  clearfix"><span class="_left">所属辖区：<em>{{datalist.address}}</em></span></div>
	<div class="fengcai">
		<span>站点风采：</span>
		<el-row :gutter="20">
			<el-col :span="6" v-for="(n, index) in imageList" :data-index="index">
		       		<img @click="open($event)" :src="n.url">
		       </el-col>
		 </el-row>
	</div>
</div>
</template>
<style>
.clearfix :after{
	overflow: hidden;
	content: ".";
	height: 0;
	visibility: hidden;
	display: none;
	clear: both;
}
.cs_zddetail{
	position: relative;
	width: 492px;
	height: 430px;
	background: #fff;
	border: 1px solid rgba(215, 215, 215, 1);
}
.cs_zddetail ._name{
	width: 492px;
	height: 49px;
	background-color: rgba(215, 215, 215, 1);
    border: none;
    padding: 0 12px;
}
.cs_zddetail ._name span{
	float: left;
	font-size: 18px;
	font-weight: 600;
	line-height: 49px;
}
.cs_zddetail ._name i{
	float: right;
	font-size: 24px;
	font-weight: 600;
	line-height: 49px;
}
.cs_zddetail ._lis{
	line-height: 31px;
	padding: 9px 30px;
	background: #fff;
	font-size: 14px;
}
.cs_zddetail ._lis ._left{
	float: left;
}
.cs_zddetail ._lis ._right{
	float: right;
}
.cs_zddetail ._lis em{
	font-style: normal;
}
.cs_zddetail ._lis ._left b{
	width: 310px;
	line-height: 31px;
	display:block;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.cs_zddetail ._lis ._left a{
}
.cs_zddetail .fengcai{
	line-height: 31px;
	padding: 0 30px;
	font-size: 14px;
}
.cs_zddetail .fengcai span{
	display: block;
	padding-bottom: 15px;
}

.cs_zddetail .fengcai img{
	width: 100%;
	height: 100%;
	display: block;
	border: none;
}
</style>
<script>
import fancyBox from 'vue-fancybox';
export default {
	data:function(){
		return {
			datalist:{},
			imageList: [],
		}
	},
	methods:{
		f_showzddetail:function(){
			const Bus = global.eventHub;
			Bus.$emit('doto_showzddetail');
		},
		open (e) {
          fancyBox(e.target,this.imageList);
        }
	},
    created:function(){
    	var that=this;
	    const Bus = global.eventHub;
	    Bus.$on('detail_zhandian', function(value) {
            console.log(value);
            that.imageList=[];
            that.datalist=value;
            var obj={width: 900, height: 675};
            if(value.imgs){
            	var arr=value.imgs.split(",");	
            	arr.forEach(function(value,index,array){
            	obj.url=value;
            	that.imageList.push(obj);
            });
            }

        });

        



    }
}
	
</script>



